body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Overlay content */
.caption {
  color: white;
  text-align: center;
  padding-top: 25%;
}

#name {
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1em;
}

#opener {
  text-transform: lowercase;
  letter-spacing: 0.2em;
}

/* Breakpoint for phones & tablets */
@media (max-width: 767.98px) {
  .caption {
    padding: 35% 0;
  }

  #opener {
    letter-spacing: 0em;
  }
}

/* Link & brand styles */
.landing-link {
  color: white;
}

.landing-link:hover {
  color: cyan;
}

.brand-icons {
  text-align: center;
  padding: 2vh 1vw;
}

.about-content {
  padding: 10% 5%;
}

.photo {
  text-align: center;
}

.description {
  padding-right: 25rem;
  text-align: left;
}

img {
  position: relative;
  -webkit-transform: translateY(5%);
          transform: translateY(5%);
  vertical-align: middle;
  border-style: none;
}

#myPhoto {
  border-radius: 50%;
  height: 200px;
}

/* Breakpoint for phones to reduce l/r padding */
@media (max-width: 575.98px) {
  .about-content {
    padding: 5% 0;
  }

  .description {
    padding-top: 3%;
  }

  #myPhoto {
    height: 120px;
  }
}

@media (max-width: 991.98px) {
  .about-content {
    padding: 5% 0;
  }

  .description {
    padding-top: 3%;
  }
}

.tech {
  margin-top: 5%;
}

.lang {
  padding: 5%;
}

#soft-title {
  text-align: left;
  font-size: 2.5rem;
  letter-spacing: 5px;
  text-transform: uppercase;
  border-bottom: 2px solid white;
  padding-bottom: 1%;
}

@media (max-width: 575.98px) {
  #soft-title {
    padding-bottom: 5%;
    font-size: 2em;
    text-align: center;
    font-weight: bold;
  }
}

#js {
  letter-spacing: 5px;
  text-transform: uppercase;
}

#py {
  letter-spacing: 5px;
  text-transform: uppercase;
}

#em {
  letter-spacing: 5px;
  text-transform: uppercase;
}

#web {
  letter-spacing: 5px;
  text-transform: uppercase;
}

#pro-header {
  text-align: right;
  font-size: 2.5rem;
  letter-spacing: 5px;
  text-transform: uppercase;
  border-bottom: 2px solid black;
  padding-bottom: 1%;
}

.myprojects {
  color: black;
  background-color: transparent;
  padding: 0 20px !important;
  margin: 10px auto;
  text-align: left;
}

.pro-titles {
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 1.5em;
  padding-bottom: 3%;
}

.project-row {
  margin-top: 5%;
}

.pro-button {
  text-align: center;
  padding-top: 5%;
}

@media (max-width: 575.98px) {
  #pro-header {
    padding-bottom: 5%;
    font-size: 2em;
    text-align: center;
    font-weight: bold;
  }

  .myprojects {
    text-align: center;
  }
}

* {
  font-family: "Raleway", sans-serif;
}

body,
html {
  margin: 0;
  width: 100%;
  height: 100%;
}

.main {
  background-image: url(/portfolio/static/media/slightly-hazy-forest.a9683aeb.jpg);
  position: relative;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.about {
  height: 70vh;
  position: relative;
}

.software,
.projects {
  height: 100vh;
  padding: 5vh 0;
  background-color: #282828;
  color: white;
  position: relative;
}

.projects {
  background-color: white;
  color: black;
  position: relative;
}

@media (max-width: 767.98px) {
  .about {
    height: 110vh;
    overflow: hidden;
  }

  .software {
    height: 150vh;
    overflow: hidden;
  }

  .projects {
    height: 150vh;
    overflow: hidden;
  }
}

